<template>
  <div class="container">
    <Header type="back" :title="t('index.邀请好友')"></Header>
    <div class="share">
      <img class="title-img" v-if="local === 'zh-TW'" src="@/assets/images/share-title.png" alt="">
      <img class="title-img" style="height:35px;" v-else src="@/assets/images/share-title-en.png" alt="">
      <img class="logo" src="@/assets/images/logo.png" @error="$setErrorImg" alt="">
      <div class="content">
        <div class="block-top">
          <!-- <div class="qrcode" style="height:149px;">
            <img v-if="qrcode" :src="qrcode" alt="">
          </div> -->
          <div class="line line1">
            <img src="@/assets/images/share-line.png" alt="">
          </div>
          <div class="line line2">
            <img src="@/assets/images/share-line.png" alt="">
          </div>
        </div>
        <div class="block-bottom">
          <div class="tip-title">{{t('index.邀请地址')}}</div>
          <div class="link">
            {{shareUrl}}
          </div>
          <div class="btn-wrap">
            <div class="btn copy-cur-href" :data-clipboard-text="shareUrl">{{t('index.複製地址')}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Header from '@/components/Header'
import { reactive, toRefs, computed, watch, onMounted } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
// import QRCode from 'qrcode'
import Clipboard from "clipboard";
import { Toast } from "vant";
import { getLang } from "@/utils/i18n";
export default {
  components: {
    Header
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      qrcode: '',
      shareUrl: '',
      local: getLang() || "zh-TW",
      initContract: () => {
        data.shareUrl = `${data.address}`
        data.initData()
      },
      initData: () => {
        // data.generateQR()
      },
      // generateQR: async () => {
      //   try {
      //     data.qrcode = await QRCode.toDataURL(data.shareUrl, {
      //       width: 149,
      //       margin: 2
      //     })
      //   } catch (err) {
      //     console.error(err)
      //   }
      // }
    })
    onMounted(() => {
      var clipboardHref = new Clipboard(".copy-cur-href");
      clipboardHref.on("success", function (e) {
        Toast(t("share.複製成功"));
        e.clearSelection();
      });
      clipboardHref.on("error", function () {
        Toast(t("share.複製失敗，請手動長按複製"));
      });
    });
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.container {
  background: url(~@/assets/images/share-bg.webp) 0 0 no-repeat;
  background-size: cover;
  min-height: 100vh;
  box-sizing: border-box;
}
.share {
  padding: 26px 28px 62px;
  .title-img {
    height: 62px;
    width: initial;
    display: block;
    margin: 0 auto;
  }
  .logo {
    width: 70px;
    height: 70px;
    margin: 18px auto 0;
    display: block;
    border-radius: 50%;
    z-index: 100;
    position: relative;
  }
  .content {
    text-align: center;
    box-sizing: border-box;
    margin: 0 auto;
    .block-top {
      background: #1a202e;
      border-radius: 15px;
      padding: 66px 0 46px;
      position: relative;
      margin: -35px 0 0;
      .line {
        width: 16px;
        height: 70px;
        display: block;
        position: absolute;
        img {
          width: 100%;
          height: 100%;
          position: relative;
          z-index: 10;
        }
        &.line1 {
          left: 44px;
          bottom: -42px;
          &::before {
            content: '';
            display: block;
            width: 16px;
            height: 16px;
            border-radius: 50%;
            position: absolute;
            left: 0;
            top: -4px;
            background: #04072b;
            z-index: 1;
          }
          &::after {
            content: '';
            display: block;
            width: 16px;
            height: 16px;
            border-radius: 50%;
            position: absolute;
            left: 0;
            bottom: -4px;
            background: #04072b;
            z-index: 1;
          }
        }
        &.line2 {
          right: 44px;
          bottom: -42px;
          &::before {
            content: '';
            display: block;
            width: 16px;
            height: 16px;
            border-radius: 50%;
            position: absolute;
            left: 0;
            top: -4px;
            background: #04072b;
            z-index: 1;
          }
          &::after {
            content: '';
            display: block;
            width: 16px;
            height: 16px;
            border-radius: 50%;
            position: absolute;
            left: 0;
            bottom: -4px;
            background: #04072b;
            z-index: 1;
          }
        }
      }
    }
    .block-bottom {
      background: #1a202e;
      border-radius: 15px;
      margin-top: 15px;
      padding: 0 23px 34px;
      .tip-title {
        font-size: 17px;
        font-family: PingFang SC;
        font-weight: bold;
        color: #ffffff;
        text-align: center;
        padding: 59px 0 23px;
      }
    }
    .link {
      background: #283146;
      border-radius: 4px;
      padding: 11px 11px;
      font-size: 14px;
      font-family: PingFang SC;
      font-weight: 500;
      color: #9798b3;
      word-break: break-all;
    }
    .btn-wrap {
      margin-top: 23px !important;
      .btn {
        width: 100%;
        background-size: 100% 100%;
        margin: 0 auto;
        height: 44px;
        line-height: 44px;
        background: linear-gradient(135deg, #3bd3ff, #3b89ff);
        border-radius: 5px;
      }
    }
  }
}
</style>
<style lang="less">
.qrcode {
  img {
    margin: 0 auto;
    display: block;
  }
}
</style>
